
import ShopBtn from '@/components/shopBtn'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getPreOrder, submitOrder } from '@/api/order'

export default {
name: 'confirmOrder',
components: {
CheckerItem,
Checker,
ShopBtn,
XInput,
Group,
XButton
},
data () {
return {
actives: [],
orderinfo: {addressinfo: {addressid: 0, name: '', mob: '', address: '', isdefault: 0}, cantoninfo: {canuserticket: 0, selticketid: 0, selticketname: '', selticketmoney: 0, totalpoint: 0, canusepoint: 0, pointmoney: 0}, productinfo: [{}], priceinfo: {productmoney: 0, activemoney: 0, ticketmoney: 0, freightmoney: 0, paymoney: 0, totalproductmoneyonly: 0, firstordertype: 0, more: {str: '', price1st: '', price2nd: ''}}},
postData: {// 当前购物车数据
preid: ''
},
isclicked:false,
cantpay:false,
isSelPoint: false,
invoiceStaus: false,
needInvoide: false,
invoicetype: 1, // 发票类型
invoicetitle: '',
invoiceduty: '',
invoicemob: '',
invoiceemail: '',
submitData: {// 订单提交数据
preid: '',
addressid: '',
ticketid: '',
point: '',
getinvoice: '',
invoicetype: '',
invoicetitle: '',
invoiceduty: '',
invoicemob: '',
invoiceemail: '',
memo:''
},

}
},
created () {
this.postData.preid = this.$route.query.preid
this.postData.tid = this.$route.query.tid
this.submitData.preid = this.postData.preid
this.getPreOrder()

window.localStorage.setItem('isFromOrder', 0)
var memoset = window.localStorage.getItem("ordermemo");
if (memoset) this.submitData.memo = memoset;
},

methods: {
close () {
this.invoiceStaus = false
this.needInvoide = false
},
clickPerson () { // 点击个人或单位
if (this.invoicetype == 1) {
this.invoicetype = 2
} else {
this.invoicetype = 1
}
},
clickInvoiceBtn () {
if (this.invoicetitle == '') {
this.$vux.toast.text('发票抬头不能为空', 'middle')
return
}
const regMail = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
if (this.invoiceemail !== '') {
if (!regMail.test(this.invoiceemail)) {
this.$vux.toast.text('邮箱有误', 'middle')
return
}
}
const reg = 11 && /^((13|14|15|16|17|18|19)[0-9]{1}\d{8})$/
if (!reg.test(this.invoicemob)) {
if (this.invoicemob === '' || this.invoicemob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
if (this.invoicetype == 2) {
const regNum = /^[A-Z0-9]*$/
if (!regNum.test(this.invoiceduty)) {
if (this.invoiceduty === '' || this.invoiceduty === null) {
this.$vux.toast.text('请填写公司税号', 'middle')
return
}
this.$vux.toast.text('公司税号有误')
return
}
}
this.invoiceStaus = false
this.needInvoide = true
},

clickToPay () {
if (this.isclicked||this.cantpay) return;
if (this.isSelPoint) {
this.submitData.point = this.orderinfo.cantoninfo.canusepoint
} else {
this.submitData.point = 0
}

if (this.needInvoide) {
this.submitData.getinvoice = 1
this.submitData.invoicetype = this.invoicetype
this.submitData.invoicetitle = this.invoicetitle
this.submitData.invoiceduty = this.invoiceduty
this.submitData.invoicemob = this.invoicemob
this.submitData.invoiceemail = this.invoiceemail

if (this.submitData.invoicetitle == '') {
this.$vux.toast.text('发票抬头不能为空', 'middle')
return
}
const regMail = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
if (this.submitData.invoiceemail !== '') {
if (!regMail.test(this.submitData.invoiceemail)) {
this.$vux.toast.text('邮箱有误', 'middle')
return
}
}
const reg = 11 && /^((13|14|15|16|17|18|19)[0-9]{1}\d{8})$/
if (!reg.test(this.submitData.invoicemob)) {
if (this.submitData.invoicemob === '' || this.submitData.invoicemob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
if (this.submitData.invoicetype == 2) {
const regNum = /^[A-Z0-9]*$/
if (!regNum.test(this.submitData.invoiceduty)) {
if (this.submitData.invoiceduty === '' || this.submitData.invoiceduty === null) {
this.$vux.toast.text('请填写公司税号', 'middle')
return
}
this.$vux.toast.text('公司税号有误')
return
}




}
} else {
this.submitData.getinvoice = 0
this.submitData.invoicetype = ''
this.submitData.invoicetitle = ''
this.submitData.invoiceduty = ''
this.submitData.invoicemob = ''
this.submitData.invoiceemail = ''
}


this.isclicked = true;

submitOrder(this.submitData)
.then(res => {
this.isclicked = false;
if (res.data.errorcode === 0) {

window.localStorage.setItem('ordermemo', "");

this.clearCar()


if (window.location.hostname === '0.0.0.0' || window.location.hostname === 'localhost') {

this.$router.replace({path: '/nowPay', query: {id: res.data.data.orderid}})
} else {
window.location.href = process.env.BASE_SELF + '/wap/index.html#/nowPay?id=' + res.data.data.orderid
}







} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.isclicked = false;
this.$vux.toast.text('网络异常！', 'middle')
})
},
clearCar () {
let ShoppingCarStr = window.localStorage.getItem('ShoppingCarStr')
let ShoppingCarArray = JSON.parse(ShoppingCarStr)
if (ShoppingCarArray) {
for (var i = ShoppingCarArray.length - 1; i >= 0; i--) {
var buyed = false
this.orderinfo.productinfo.forEach(function (x, y, z) {
if (ShoppingCarArray[i].skuid == x.skuid) {
buyed = true
}
})
if (buyed) { // 已购
ShoppingCarArray.splice(i, 1)
}
}
}
window.localStorage.setItem('ShoppingCarStr', JSON.stringify(ShoppingCarArray))
},

getPreOrder () {
getPreOrder(this.postData)
.then(res => {
if (res.data.errorcode === 0) {

this.orderinfo = res.data.data
this.cantpay = this.orderinfo.addressinfo.cantpay

this.submitData.ticketid = this.orderinfo.cantoninfo.selticketid
this.submitData.addressid = this.orderinfo.addressinfo.addressid

this.orderinfo.productinfo.map(item => {
if (item.activityname) {
this.actives.push(item.activityname)
}
})
} else {
if (res.data.errorcode == 10099) {
this.$router.push({ path: '/shoppingtrolley' })
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
clickPoint () {
this.isSelPoint = !this.isSelPoint
if (this.isSelPoint) {
this.orderinfo.priceinfo.paymoney = (1 * this.orderinfo.priceinfo.paymoney - 1 * this.orderinfo.cantoninfo.pointmoney).toFixed(2)
} else {
this.orderinfo.priceinfo.paymoney = (1 * this.orderinfo.priceinfo.paymoney + 1 * this.orderinfo.cantoninfo.pointmoney).toFixed(2)
}
},
clickAddress () {
window.localStorage.setItem('isFromOrder', this.postData.preid)
window.localStorage.setItem('ordermemo', this.submitData.memo)
this.$router.push({ path: '/address' })

},
clickTicket () {
window.localStorage.setItem('ordermemo', this.submitData.memo)

this.$router.push({path: '/coupon', query: { preid: this.postData.preid,tid:this.orderinfo.cantoninfo.selticketid}})


}
}
}



